import React, { ReactNode, HTMLAttributes } from 'react';
import { createComponent } from '../lib';
import type { JSX } from '@vime/core/dist/types';

import { 
  VmAudio,
  VmFile 
} from '@vime/core';

import { define } from '../lib';
  

define('vm-audio', VmAudio);
define('vm-file', VmFile);
  

export interface AudioProps extends JSX.VmAudio, HTMLAttributes<HTMLVmAudioElement> {
  children?: ReactNode | ReactNode[]
};

export default createComponent<HTMLVmAudioElement, AudioProps>(
  'vm-audio',
  new Set(['willAttach','crossOrigin','preload','disableRemotePlayback','mediaTitle']),
);
  